import React from "react";
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Container from "@material-ui/core/Container/Container";
import { makeStyles } from "@material-ui/core";
import FeedbackForm from "../../components/FeedbackForm";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography/Typography";

const useStyles = makeStyles(theme => ({
  featuresMainContainer: {
    padding: "0!important"
  },
  featuresCommonContainer: {
    backgroundColor: "#e3e8ec",
    paddingBottom: "40pt"
  },
  featuresMainDescr: {
    fontFamily: "RostelecomBasisMedium!important",
    marginBottom: "10pt!important"
  },
  featuresAdditionalDescr: {
    fontSize: "1rem!important"
  },
  featuresTitle: {
    marginTop: "0pt!important",
    paddingTop: "50pt",
    paddingBottom: "14pt"
  },
  integrationDescrContainer: {
    backgroundColor: "#ffffff",
    padding: "40pt",
    marginBottom: "20pt",
    borderRadius: "0.4%"
  },
  [theme.breakpoints.down("xs")]: {
    integrationDescrContainer: {
      padding: "0",
      backgroundColor: "#e3e8ec"
    }
  }
}));

function Integrations() {
  const classes = useStyles();

  return (
    <Container maxWidth={false} className={classes.featuresMainContainer}>
      <Header rightLinks={<HeaderLinks />} fixed />
      <Container maxWidth={false} className={classes.featuresCommonContainer}>
        <Container maxWidth={"lg"}>
          <h1 className={classes.featuresTitle}>Интеграции</h1>
          <Grid
            container
            item
            md={12}
            justify={"flex-start"}
            alignItems={"center"}
            className={classes.integrationDescrContainer}
          >
            <Typography
              variant={"h6"}
              color={"textPrimary"}
              className={classes.featuresMainDescr}
            >
              Интеграция с 1С
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
              Внешняя обработка обеспечивает обмен электронными, юридически
              значимыми документами непосредственно из пользовательского
              интерфейса 1С, а также последующую выгрузку Счетов-фактур, Актов,
              Накладных, Счетов на оплату из системы «1С» в Национальную систему
              «Электронный документ», ПАО «Ростелеком» и отправку их
              контрагентам.
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
              Основным предназначением внешней обработки является преобразование
              электронного Счета-фактуры, Акта, Накладной в формат ФНС,
              утвержденный приказом ФНС от 24 марта 2016 г. N ММВ-7-15/155@ и
              реализованный в Национальной системе «Электронный документ» ПАО
              «Ростелеком».
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
              Внешняя обработка может работать в разных конфигурациях 1С.
              Установка внешней обработки не вносит изменений в работу типовой
              конфигурации 1С. Конфигурация 1С не снимается с гарантийной
              поддержки.
            </Typography>
          </Grid>
          <Grid
            container
            item
            md={12}
            justify={"flex-start"}
            alignItems={"center"}
            className={classes.integrationDescrContainer}
            id={"SapOracleContainer"}
          >
            <Typography
              variant={"h6"}
              color={"textPrimary"}
              className={classes.featuresMainDescr}
            >
              Интеграция с Oracle и SAP
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
              Крупные организации, использующие иностранные ERP-системы, могут
              заказать установку и настройку Адаптеров для упрощения работы по
              обмену документами с контрагентами. Отправленные от контрагентов
              документы будут поступать в интерфейс ERP системы для дальнейшей
              их обработки, а также доступна возможность отправки документов
              клиентам из интерфейса ERP системы.
            </Typography>
          </Grid>
        </Container>
      </Container>
      <FeedbackForm />
      <Footer />
    </Container>
  );
}

export default Integrations;
