import React from "react";
import Container from "@material-ui/core/Container/Container";
import { makeStyles, withStyles } from "@material-ui/core";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import SwipeableViews from "react-swipeable-views";
import iconCloud from "../../img/svg/cloud.svg";
import iconFlashDrive from "../../img/svg/flash-drive.svg";
import iconIerarchy from "../../img/svg/ierarchy.svg";
import iconPlanet from "../../img/svg/planet.svg";
import FeedbackForm from "../../components/FeedbackForm";
import Box from "@material-ui/core/Box/Box";
import PropTypes from "prop-types";
import Header from "components/Header/Header.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Footer from "components/Footer/Footer.jsx";

const dashboardRoutes = [];

const useStyles = makeStyles(theme => ({
  featuresMainContainer: {
    padding: "0!important"
  },
  featuresContainer: {
    paddingTop: "30pt!important"
  },
  featuresCommonContainer: {
    backgroundColor: "#e3e8ec",
    paddingBottom: "90pt"
  },
  featuresMainDescr: {
    fontFamily: "RostelecomBasisMedium!important",
    marginBottom: "20pt!important",
    lineHeight: "18pt"
  },
  featuresAdditionalDescr: {
    fontSize: "1rem!important",
    width: "96%"
  },
  featuresTitle: {
    marginTop: "6pt!important"
  },
  featuresTabsDescr: {
    marginTop: "15pt!important"
  },
  advantagesMainContainer: {
    paddingLeft: 0,
    paddingRight: 0,
    paddingTop: "42pt",
    paddingBottom: "42pt",
    backgroundColor: "#7b939b"
  },
  advantagesMainDescr: {
    fontFamily: "RostelecomBasisMedium!important",
    fontSize: "1.2rem!important",
    marginTop: "15pt!important",
    marginBottom: "15pt!important"
  },
  advantagesAdditionalDescr: {
    fontSize: "1rem!important",
    width: "80%",
    textAlign: "center"
  },
  advantagesItemContainer: {
    height: "320px"
  },
  fullHeight: {
    height: "100%"
  },
  textContainers: {
    alignContent: "flex-start"
  },
  functionalPageTextContainer: {
    marginTop: "50pt"
  },
  [theme.breakpoints.down("sm")]: {
    textContainers: {
      marginBottom: "25pt"
    },
    functionalPageTextContainer: {
      marginTop: "0"
    }
  }
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      <Box p={0}>{children}</Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`
  };
}

const StyledTabs = withStyles({
  indicator: {
    "& > div": {
      backgroundColor: "#ff4f12"
    }
  },
  flexContainer: {
    borderBottom: "2px solid #d2d5d8"
  }
})(props => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);

const StyledTab = withStyles(theme => ({
  root: {
    textTransform: "none",
    fontFamily: "RostelecomBasisBold",
    fontSize: theme.typography.pxToRem(16)
  }
}))(props => <Tab {...props} />);

function Functional() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  function selectOfNeededTab() {
    let neededTab = document.location.hash.substr(-2);
    switch (neededTab) {
      case "#2":
        handleChangeIndex(1);
        break;
      default:
        handleChangeIndex(0);
    }
  }

  window.addEventListener("hashchange", selectOfNeededTab, false);

  function handleChange(event, newValue) {
    setValue(newValue);
  }
  function handleChangeIndex(index) {
    setValue(index);
  }
  return (
    <Container maxWidth={false} className={classes.featuresMainContainer}>
      <Header routes={dashboardRoutes} rightLinks={<HeaderLinks />} fixed />
      <Container maxWidth={false} className={classes.featuresCommonContainer}>
        <Container className={classes.featuresContainer} maxWidth={"lg"}>
          <Grid container justify={"space-between"} alignItems={"flex-start"}>
            <h1 className={classes.featuresTitle}>Возможности</h1>
            <StyledTabs value={value} onChange={handleChange}>
              <StyledTab label={"В личном кабинете"} {...a11yProps(0)} />
              <StyledTab label={"С контрагентами"} {...a11yProps(1)} />
            </StyledTabs>
          </Grid>
          <SwipeableViews
            axis={"x"}
            index={value}
            onChangeIndex={handleChangeIndex}
          >
            <TabPanel
              value={value}
              index={0}
              className={classes.featuresTabsDescr}
            >
              <Grid container item justify={"space-between"} md={12}>
                <Grid container item md={4} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    Создавать и добавлять
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Можно создавать первичные документы, такие как:
                    Счета-фактуры, Акты, Накладные, УПД. А также добавлять любые
                    другие типы документов: Счета, Договоры, Акты сверки,
                    Приложения и Иные
                  </Typography>
                </Grid>
                <Grid container item md={4} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    Подписывать и отправлять
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Подготовленный документ можно подписать и отправить отдельно
                    или в составе пакета. Пакетная отправка документов не
                    ограничена по количеству и не тарифицируется дополнительно
                  </Typography>
                </Grid>
                <Grid container item md={4} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    Согласовывать
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Созданные и добавленные документы можно согласовать с
                    коллегами или руководством перед отправкой контрагенту
                  </Typography>
                </Grid>
              </Grid>
              <Grid
                container
                item
                justify={"space-between"}
                md={12}
                className={classes.functionalPageTextContainer}
              >
                <Grid container item md={4} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    Аннулировать
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Любой подписанный документ можно аннулировать. Аннулирование
                    доступно как одностороннее, если контрагент ещё не подписал
                    документ, так и двухстороннее, если контрагент подписал
                    документ
                  </Typography>
                </Grid>
                <Grid container item md={4} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    Следить за состоянием
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Как только контрагент откроет отправленный ему документ его
                    состояние мгновенно изменится на «Доставлен», а после
                    подписания на «Подписан»
                  </Typography>
                </Grid>
                <Grid container item md={4} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    Работать без подписи
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Если не требуется подписывать документы, то имеется
                    возможность зарегистрироваться в НСЭД без электронной
                    подписи. Пользователю без электронной подписи доступны все
                    возможности системы, за исключением функции «Подписания»
                  </Typography>
                </Grid>
              </Grid>
            </TabPanel>
            <TabPanel
              value={value}
              index={1}
              className={classes.featuresTabsDescr}
            >
              <Grid container item justify={"space-between"} md={12}>
                <Grid container item md={5} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    Внутри сети
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Если контрагент использует НСЭД, то ему не требуется
                    отправлять приглашения и дожидаться его принятия.
                    Возможность отправки документов будет доступна сразу после
                    регистрации
                  </Typography>
                </Grid>
                <Grid container item md={6} className={classes.textContainers}>
                  <Typography
                    variant={"h6"}
                    color={"textPrimary"}
                    className={classes.featuresMainDescr}
                  >
                    В роуминге
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.featuresAdditionalDescr}
                  >
                    Если контрагент использует другую систему ЭДО, можно
                    самостоятельно настроить роуминговое соединение с ним, без
                    привлечения технической поддержки Оператора ЭДО. Процесс
                    займет не более 2-3 минут. После настройки роуминга с
                    контрагентом, можно отправлять любые документы в любом
                    формате.
                  </Typography>
                </Grid>
              </Grid>
            </TabPanel>
          </SwipeableViews>
        </Container>
      </Container>
      <Container maxWidth={false} className={classes.advantagesMainContainer}>
        <Container maxWidth={"lg"} className={classes.fullHeight}>
          <Grid
            container
            justify={"center"}
            alignItems={"center"}
            className={classes.fullHeight}
          >
            <Grid
              item
              container
              md={3}
              justify={"flex-start"}
              alignItems={"center"}
              direction={"column"}
              className={classes.advantagesItemContainer}
            >
              <img src={iconFlashDrive} />
              <Typography
                variant={"subtitle1"}
                color={"textSecondary"}
                className={classes.advantagesMainDescr}
              >
                С ЭЦП и без неё
              </Typography>
              <Typography
                variant={"subtitle2"}
                color={"textSecondary"}
                className={classes.advantagesAdditionalDescr}
              >
                Удобная работа с ЭЦП при подписании документов с контрагентами,
                а также без неё при согласовании документов с коллегами
              </Typography>
            </Grid>
            <Grid
              item
              container
              md={3}
              justify={"flex-start"}
              alignItems={"center"}
              direction={"column"}
              className={classes.advantagesItemContainer}
            >
              <img src={iconIerarchy} />
              <Typography
                variant={"subtitle1"}
                color={"textSecondary"}
                className={classes.advantagesMainDescr}
              >
                Иерархия
              </Typography>
              <Typography
                variant={"subtitle2"}
                color={"textSecondary"}
                className={classes.advantagesAdditionalDescr}
              >
                Можно заводить обособленные подразделения компании, выстраивать
                иерархию, контролировать их ЭДО и собирать статистику
              </Typography>
            </Grid>
            <Grid
              item
              container
              md={3}
              justify={"flex-start"}
              alignItems={"center"}
              direction={"column"}
              className={classes.advantagesItemContainer}
            >
              <img src={iconPlanet} />
              <Typography
                variant={"subtitle1"}
                color={"textSecondary"}
                className={classes.advantagesMainDescr}
              >
                Автороуминг
              </Typography>
              <Typography
                variant={"subtitle2"}
                color={"textSecondary"}
                className={classes.advantagesAdditionalDescr}
              >
                ЭДО с контрагентами, работающими в разных системах, через
                роуминг и без привлечения технической поддержки
              </Typography>
            </Grid>
            <Grid
              item
              container
              md={3}
              justify={"flex-start"}
              alignItems={"center"}
              direction={"column"}
              className={classes.advantagesItemContainer}
            >
              <img src={iconCloud} />
              <Typography
                variant={"subtitle1"}
                color={"textSecondary"}
                className={classes.advantagesMainDescr}
              >
                Облачная ЭЦП
              </Typography>
              <Typography
                variant={"subtitle2"}
                color={"textSecondary"}
                className={classes.advantagesAdditionalDescr}
              >
                Можно использовать «облачную» электронную подпись для подписания
                документов на мобильных устройствах
              </Typography>
            </Grid>
          </Grid>
        </Container>
      </Container>
      <FeedbackForm />
      <Footer />
    </Container>
  );
}

export default Functional;
