import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Parallax from "components/Parallax/Parallax.jsx";

import profilePageStyle from "assets/jss/material-kit-react/views/profilePage.jsx";

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CustomInput from "components/CustomInput/CustomInput.jsx";
import Button from "components/CustomButtons/Button.jsx";
import SnackbarContent from "components/Snackbar/SnackbarContent.jsx";
import warning from '@material-ui/icons/Warning';
import checkAnother from  '@material-ui/icons/Check';
const offsetTop = {
  marginTop: '80px'
};
const ExpansionPanelStyles = {
  color: 'rgba(0, 0, 0, 0.87)',
  fontSize: '0.975rem',
  fontWeight: '400',
  fontFamily: '"RostelecomBasis-Light","Roboto", "Helvetica", "Arial", sans-serif',
  lineHeight: '1.46429em',
  margin: '0',
  display: 'block',
};
const formTitle = {
  width: '100%',
  textAlign:'center'
};
const fontFamily = {
  fontFamily: '"RostelecomBasis-Light","Roboto", "Helvetica", "Arial", sans-serif',
  fontWeight: 'bold'
};
class Questions extends React.Component {
    componentWillMount(){
      document.body.style.overflowY = 'scroll';
    }
  render() {
    const { classes, ...rest } = this.props;
    return (
      <div>
        <Header
          rightLinks={<HeaderLinks />}
          fixed
          {...rest}
        />
        <Parallax small filter image={require("assets/img/questions.jpg")} />
        <div className={classNames(classes.main, classes.mainRaised)}>
          <div>
            <div className={classes.container}>
              <GridContainer justify="center">
                <GridItem xs={12} sm={12} md={6} style={offsetTop}>
                  <div className={classes.profile}>
                    <div className={classes.name}>
                      <h3 className={classes.title}>Вопрос-Ответ</h3>
                    </div>
                  </div>
                </GridItem>
              </GridContainer>
              <GridContainer justify='center' style={{marginTop: '20px'}}>
                <GridItem xs={12} sm={12} md={12}>
                  <ExpansionPanel>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography className={classes.heading} style={fontFamily}>Как отправить документы контрагенту, подключенному к другой системе ЭДО?</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails style={ExpansionPanelStyles}>
                        <p>В НСЭД реализован простой и удобный способ обмена документами с контрагентами, зарегистрированными в разных системах ЭДО, без привлечения технической поддержки Оператора ЭДО при создании Роумингового соединения.</p>
                        <p>Пошаговые действия:</p>
                        <ol>
                          <li><b>Узнать Идентификатор ЭДО контрагента.</b>
                            <p>Попросите вашего контрагента отправить Идентификатор ЭДО их организации. В большей части случаев, Идентификатор ЭДО находится в разделе «Реквизиты» личного кабинета системы ЭДО. Вид идентификатора: 2RT-234rt78956-34567098dr-384657gl09-456hgfp091. Если не удается его обнаружить, то контрагенту следует обратиться на линию тех.поддержки своего Оператора ЭДО, они подскажут куда смотреть.</p>
                          </li>
                          <li><b>Отправить приглашение контрагенту.</b>
                            <p>После того как контрагент сообщил свой Идентификатор ЭДО, следует направить им приглашение для создания Роумингового соединения. Зайдите в свой личный кабинет системы «НСЭД», перейдите в раздел «Контрагенты», в левом боковом меню нажмите «Добавить контрагента РОУМИНГ». В открывшемся окне введите требуемые данные (min.: Наименование, ИНН, Идентификатор ЭДО; max.: Заполните всё) и нажмите «Добавить». Дождитесь ответа контрагента. Рабочее соединение будет иметь статус «Роуминг».</p>
                          </li>
                          <li><b>Отправить документы.</b>
                            <p>Зайдите в свой личный кабинет системы «НСЭД», перейдите в раздел «Документы», в левом боковом меню нажмите «Отправить документы». В открывшемся окне, поля «Кому» введите данные (Наименование или ИНН) контрагента, выберите нужного. Кнопками «Добавить» и «Создать» прикрепляйте и создавайте нужные документы. Когда письмо с вложениями будет готово, нажмите «Подписать и отправить», документы подпишутся и отправятся контрагенту.</p>
                          </li>
                        </ol>
                    </ExpansionPanelDetails>
                  </ExpansionPanel>
                  <ExpansionPanel>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography className={classes.heading} style={fontFamily}>В какие системы ЭДО контрагентов можно отправлять документы?</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails style={ExpansionPanelStyles}>
                        <p>Любая система ЭДО которая была подключена к Роуминговому Центру Ростелеком должна обеспечивать электронный документооборот через Роуминговые соединения. Список подключенных к Роуминговому центру Ростелеком Операторов ЭДО можно скачать и посмотреть на сайте ФНС. <a target={'_blank'} href={'https://www.nalog.ru/rn77/taxation/submission_statements/el_count/'}>Ссылка</a></p>
                    </ExpansionPanelDetails>
                  </ExpansionPanel>
                  <ExpansionPanel>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography className={classes.heading} style={fontFamily}>Как отправить документы контрагенту?</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails style={ExpansionPanelStyles}>
                        <p>Если контрагент зарегистрирован в НСЭД, то процесс добавления контрагента займет несколько секунд. <br/>Пошаговые действия:</p>
                        <ol>
                          <li><b>Добавить контрагента.</b>
                            <p>Зайдите в свой личный кабинет системы «НСЭД», перейдите в раздел «Контрагенты», в левом боковом меню нажмите «Добавить контрагента». В открывшемся окне, поля «Поиск» введите данные (Наименование или ИНН) выберите и нажмите «Добавить».</p>
                          </li>
                          <li><b>Отправить документы.</b>
                            <p>Зайдите в свой личный кабинет системы «НСЭД», перейдите в раздел «Документы», в левом боковом меню нажмите «Отправить документы». В открывшемся окне, поля «Кому» введите данные (Наименование или ИНН) контрагента, выберите нужного. Кнопками «Добавить» и «Создать» прикрепляйте и создавайте нужные документы. Когда письмо с вложениями будет готово, нажмите «Подписать и отправить», документы подпишутся и отправятся контрагенту.</p>
                          </li>
                        </ol>
                    </ExpansionPanelDetails>
                  </ExpansionPanel>
                  <ExpansionPanel>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography className={classes.heading} style={fontFamily}>Какая электронная подпись подходит для работы в НСЭД?</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails style={ExpansionPanelStyles}>
                        <p>Для работы в НСЭД подойдёт любая усиленная квалифицированная электронная подпись, выданная любым удостоверяющим центром, аккредитованным в Минкомсвязь России. <a href={'http://e-trust.gosuslugi.ru/CA'}  target={'_blank'}>Список аккредитованных УЦ</a></p>
                    </ExpansionPanelDetails>
                  </ExpansionPanel>
                  <ExpansionPanel>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography className={classes.heading} style={fontFamily}>Какие документы можно подписывать и отправлять контрагентам?</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails style={ExpansionPanelStyles}>
                        <p>В НСЭД вы можете обменяться с контрагентами любыми документами в любом формате. Важно знать, что согласно действующему законодательству, обмен счет-фактурой осуществляется только в формате XML, утвержденном ФНС (счет-фактуру можно создать прямо в НСЭД).</p>
                    </ExpansionPanelDetails>
                  </ExpansionPanel>
                  <ExpansionPanel>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography className={classes.heading} style={fontFamily}>Имеется ли интеграция с 1С?</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails style={ExpansionPanelStyles}>
                        <p>Компания Ростелеком разработала удобное и простое средство для работы в 1С.</p>
                        <p>Внешняя обработка обеспечивает обмен электронными юридически значимыми документами непосредственно из пользовательского интерфейса 1С, а также последующую выгрузку Счетов-фактур, Актов, Накладных, Счетов на оплату из системы «1С» в НСЭД и отправку их контрагентам.</p>
                        <p>Основным предназначением внешней обработки является преобразование электронного Счета-фактуры, Акта, Накладной в формат ФНС, утвержденный приказом ФНС от 24 марта 2016 г. N ММВ-7-15/155@ и реализованный в Национальной системе «Электронный документ», ПАО «Ростелеком».</p>
                        <p>Внешняя обработка может работать в разных конфигурациях 1С. Установка внешней обработки не вносит изменений в работу типовой конфигурации 1С. Конфигурация 1С не снимается с гарантийной поддержки.</p>
                    </ExpansionPanelDetails>
                  </ExpansionPanel>
                  <ExpansionPanel>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography className={classes.heading} style={fontFamily}>Будут ли доступны для просмотра и скачивания документы в НСЭД после окончания срока действия электронной подписи?</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails style={ExpansionPanelStyles}>
                        <p>Да. В этом случае, потребуется зайти в свой личный кабинет системы «НСЭД», по Логину/Паролю. После авторизации документы будут доступны для скачивания и просмотра.</p>
                    </ExpansionPanelDetails>
                  </ExpansionPanel>
                </GridItem>
              </GridContainer>
              <h4 style={{textAlign: 'center', marginTop: '30px'}}>Если остались вопросы, воспользуйтесь формой для обращения.</h4>
              <GridContainer justify="center">
                <GridItem cs={12} sm={12} md={8}>
                  <h3 className={classes.title} style={formTitle}>Работайте с нами!</h3>
                  <form className={'feedbackForm'}>
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={6}>
                        <CustomInput
                          labelText="Ваше имя"
                          id="name"
                          formControlProps={{
                            fullWidth: true
                          }}
                          name={'name'}
                          required={true}
                        />
                      </GridItem>
                      <GridItem xs={12} sm={12} md={6}>
                        <CustomInput
                          labelText="Ваш E-mail"
                          id="email"
                          formControlProps={{
                            fullWidth: true
                          }}
                          name={'Email'}
                          required={true}
                        />
                      </GridItem>
                      <GridItem xs={12} sm={12} md={12}>
                        <CustomInput
                          labelText="Напишите ваше сообщение..."
                          id="message"
                          formControlProps={{
                            fullWidth: true,
                            className: classes.textArea
                          }}
                          inputProps={{
                            multiline: true,
                            rows: 5
                          }}
                          name={'message'}
                        />
                      </GridItem>
                      <GridContainer justify="flex-start" className={'ieTopMenuFix'}>
                        <GridItem
                          xs={12}
                          sm={12}
                          md={4}
                          className={classes.textCenter}
                        >
                          <Button color="rose" style={{marginLeft:'15px'}} className={'sendMailBtn'} type={'submit'}>Отправить</Button>
                        </GridItem>
                      </GridContainer>
                    </GridContainer>
                  </form>
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <SnackbarContent
                    className={'beforeSendNotif sendInfoHidden'}
                    message={
                      <span>
                        <b>Ваше сообщение отправляется:</b> пожалуйста подождите несколько секунд!
                      </span>
                    }
                    close
                    color="info"
                    icon="info_outline"
                  />
                  <SnackbarContent
                    className={'sendSuccessNotif sendInfoHidden'}
                    message={
                      <span>
                        <b>Сообщение успешно отправлено</b>
                      </span>
                    }
                    close
                    color="success"
                    icon={checkAnother}
                  />
                  <SnackbarContent
                    className={'sendErrorNotif sendInfoHidden'}
                    message={
                      <span>
                        <b>Ошибка</b> Попробуйте отправить ещё раз
                      </span>
                    }
                    close
                    color="warning"
                    icon={warning}
                  />
                </GridItem>
              </GridContainer>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default withStyles(profilePageStyle)(Questions);
